.slider {
  cursor: pointer;
  background-color: var(--gray-radiobtn-w);
  border-radius: 34px;
  transition: all .4s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.slider:before {
  content: "";
  height: 26px;
  width: 26px;
  background-color: var(--background-w);
  border-radius: 50%;
  transition: all .4s;
  position: absolute;
  bottom: 4px;
  left: 4px;
}

input:checked + .slider {
  background: linear-gradient(var(--green) ), linear-gradient(var(--green) );
}

input:checked + .slider:before {
  transform: translateX(26px);
}

